import React, { useEffect } from 'react'
import { Admin } from '../components'
import { useQuery } from 'react-query'
import axios from 'axios'

const AdminPage = () => {
  const backend = process.env.REACT_APP_BACKEND_SERVER;
  
  //   
  const fetchUsers = async () => {
    const response = await axios.get(`${backend}/login/pendingUsers`, {
      withCredentials : true
    });
    // console.log("   ",response);
    return response.data;
  }

  //     1 
  const approveUser = async (userId) => {
    try {
      await axios.put(`${backend}/login/pendingUsers/${userId}`, {
        access: 1
      }, { withCredentials: true });
      alert("  .")
    } catch (error) {
      console.log(error);
    }
  }
  
  //   
  const rejectUser = async (userId) => {
    try {
      await axios.delete(`${backend}/login/pendingUsers/${userId}`, {
        withCredentials : true
      });
      alert("  .")
    } catch (error) {
      console.log(error);
    }
  }
  
  // useQuery  
  const { data : users, isLoading, isError, error } = useQuery('users', fetchUsers);

  //    
  useEffect(()=> {
    if (users) {
      console.log("  :", users);
    }
    if (isError) {
      console.error("  :", error);
    }
  }, [users, isError, error]);

  if (isLoading) return <p>Loading...</p>;
  if (isError) return <p> </p>

  return (
    <>
      <Admin>
        <h1>  </h1>
        <div className='usersContainer'>
           {users && users.map(user => (
              <div key={user.id} className='user-item'>
                <span className="nickname">{user.nickname}</span>
                <div>
                    <button onClick={()=>{approveUser(user.id)}}></button>
                    <button onClick={()=>{rejectUser(user.id)}}></button>
                </div>
              </div>
          ))}
        </div>
      </Admin>
    </>
  )
}

export default AdminPage